	module.exports = {"data":{"markdownRemark":{"html":"<p><strong>Are you restyling your home, kitchen, hotel or restaurant</strong> – or just looking for that one lamp or rug to finish off your own scheme – we can help.</p>\n<p>We offer a complete Interior Design service to domestic and commercial clients.</p>\n<p>Sue Dann Interiors is a BIID recognised Interior Design practice established for 15 years led by a professional and creative designer with over 30 years design experience.</p>\n<hr>\n<p>The company was founded in 1998 in Buckinghamshire and serviced the Home Counties and London for seven years.</p>\n<p>After a move to Bath in 2005 we have undertaken many projects in Somerset and Wiltshire.</p>\n<p>We now have our main studio / showroom in Pewsey in Wiltshire which is ideally located for clients anywhere along the M4 corridor from Bath to London.</p>"},"galleriesJson":{"images":[{"title":"","path":"home01.jpg"},{"title":"","path":"home03.jpg"},{"title":"","path":"home05.jpg"},{"title":"","path":"home06.jpg"},{"title":"","path":"home04.jpg"},{"title":"","path":"home07.jpg"}]}},"pathContext":{}}

